<template>
  <el-dialog :visible="dialog" @close="close"
             custom-class="common-dialog"
             width="630" :before-close="beforeClose">
    <div class="dialog-header">
      <i class="el-icon-caret-right icon"></i>

      <h2 class="title"></h2>

      <img src="../../../assets/img/close.png" class="close" @click="close">

    </div>
    <div class="dialog-form">
      <el-form label-width="100px">

        <el-form-item label="" class="one-line-item">
          <el-date-picker v-model="form.SubmitTime" type="datetime"
                          placeholder=""
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="" class="one-line-item">
          <el-select v-model="form.OperatorID" placeholder="" @change="getAisleCodeAndArea">
            <el-option v-for="(item, index) in userList" :key="index"
                       :label="`${item.UserID}-${item.RealName}`" :value="item.UserID">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" class="one-line-item">
          <el-input v-model.trim="form.AisleCode" placeholder="" disabled></el-input>
        </el-form-item>


        <el-form-item label="" prop="Area" class="one-line-item">
          <el-select v-model="form.Area" placeholder="" multiple>
            <el-option :label="item.BridgeCode" :value="item.BridgeCode"
                       v-for="(item,index) in AreaList" :key="index">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="Attribute" class="one-line-item">
          <el-select v-model="form.Attribute" placeholder="">
            <el-option label="" value="regular"></el-option>
            <el-option label="" value="reserve"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="StartTime" class="one-line-item">
          <el-date-picker v-model="form.StartTime" type="datetime"
                          placeholder=""
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="" prop="StopTime" class="one-line-item">
          <el-date-picker v-model="form.StopTime" type="datetime"
                          placeholder=""
                          format="yyyy-MM-dd HH:mm:ss"
                          value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="" prop="DispatcherID" class="one-line-item">
          <el-input v-model.trim="form.DispatcherID" placeholder=""></el-input>
        </el-form-item>

      </el-form>

      <el-button type="primary" class="from-confirm-btn" @click="toSearch">
        
      </el-button>
    </div>

  </el-dialog>
</template>
<script>
  import {mapState} from 'vuex'
  import {doCopyObj} from '../../../utils/public'
  import {dispatchService} from '../../../http/service'
  import {initSchedulingField} from '../../../http/filed'

  export default {
    props: ['getScheduling', 'userList'],
    computed: {
      ...mapState(['dialog'])
    },
    data() {
      return {
        form: {
          Area: []
        },
        AreaList: []
      }
    },
    methods: {
      // 1
      getAisleCodeAndArea() {
        dispatchService.initScheduling({
          params: initSchedulingField(this.form.OperatorID),
          cb: (res) => {
            this.AreaList = res
            if (res.length > 0) {
              this.form.AisleCode = res[0].AisleCode
              this.form = doCopyObj(this.form)
            } else {
              this.form.AisleCode = ''
              this.form = doCopyObj(this.form)
            }
          }
        })
      },
      // 11
      beforeClose() {
        this.setDialog(false)
      },
      // 22
      setDialog(dialog) {
        this.$store.dispatch('setDialog', dialog)
      },
      // 33
      close() {
        this.setDialog(false)
      },
      toSearch() {
        this.getScheduling(this.form)
        this.setDialog(false)
      }
    }
  }
</script>
