<template>
  <el-dialog :visible="editDialog" @close="close"
             custom-class="common-dialog"
             width="630" :before-close="beforeClose">
    <div class="dialog-header">
      <i class="el-icon-caret-right icon"></i>

      <h2 class="title"></h2>

      <img src="../../../assets/img/close.png" class="close" @click="close">

    </div>
    <div class="dialog-form">
      <el-form label-width="100px" :status-icon="true" :model="form"
               :rules="rules" ref="form">

        <el-form-item label="" prop="RealName" class="data-item">
          <el-input v-model.trim="form.data1" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" prop="UserCode" class="one-line-item">
          <el-input v-model.trim="form.data1" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" prop="Password" class="one-line-item">
          <el-input v-model.trim="form.data1" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" prop="Mobile" class="one-line-item">
          <el-input v-model.trim="form.data1" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" prop="DepartID" class="data-item">
          <el-select v-model="form.command" placeholder="">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="JobID" class="one-line-item">
          <el-select v-model="form.command" placeholder="">
            <el-option label="001" value="001"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="" class="one-line-item">
            <el-button type="primary" class="from-confirm-btn">
                
            </el-button>
            <el-button type="" class="from-confirm-btn">
                
            </el-button>
        </el-form-item>
    
      </el-form>
    </div>

  </el-dialog>
</template>
<script>
  import {mapState} from 'vuex'

  export default {
    props: ['isChecked'],
    computed: {
      ...mapState(['editDialog'])
    },
    data() {
      return {
        form: {},
        rules: {
          title: [
            {required: true, message: "25", max: 25, trigger: "blur"}
          ],
          op_company_id: [
            {required: true, message: "", trigger: "change"}
          ],
          picture: [
            {required: true, message: "", trigger: "change"}
          ],
          link_address: [
            {required: true, message: "", trigger: "blur"}
          ]
        }
      }
    },
    methods: {
      // 11
      beforeClose() {
        this.setEditDialog(false)
      },
      // 22
      setEditDialog(editDialog) {
        this.$store.dispatch('setEditDialog', editDialog)
      },
      // 33
      close() {
        this.setEditDialog(false)
      }
    },
    watch: {
      isChecked() {
        this.checked = this.isChecked
      }
    }
  }
</script>
<style scoped lang="less">
  .common-dialog {

  }
</style>
